    .toolTipSave {
        color: #FFFFFF;
        height: 44px;
        background: #8651D9 0% 0% no-repeat padding-box;
        opacity: 0.64;
    }
    
    .checkboxToolTip {
        width: 14px;
        height: 14px;
        border: 1px solid #FFFFFF;
        border-radius: 3px;
    }
    /*footer  */
    /* select */
    
    .select {
        height: 45px;
    }
    
    .select>a,
    .select>a:hover {
        color: #8181A5;
        padding: 10px;
        display: inline-block;
        width: 100%;
    }
    
    .select:hover {
        background: #E8EAEC4D;
    }

    .mode-icon{
        padding: 10px;
        margin: 3px;
        display: inline-block!important;
        min-width: 42px!important;
        height: 42px;
        text-align: center;
    }
